}

.bsa-header {
  display: flex;
  align-items: center;
  padding: 0 12px;
  position: fixed;
  top: 0;
  left: 250px;
  right: 0;
  height: 80px;
  background: #fff;
  border-bottom: 1px solid rgba(127, 113, 113, 0.08);
  z-index: 80;
  list-style: none;
  margin: 0;
  /*默认白色需要加边框*/
}

.bsa-header > li {
  background: inherit;
  height: inherit;
}

.bsa-header .bsa-header-item {
  background: inherit;
  font-size: 24px;
  cursor: pointer;
  padding: 0 20px;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #5f5f5f;
}

@media (max-width: 991.98px) {
  .bsa-header .bsa-header-item {
