    padding: 0 8px;
  }
}

.bsa-header .bsa-header-item .bsa-user-avatar {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

