.bsa-sidebar {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 250px;
  z-index: 100;
  transition: 0.3s transform;
  transform: translateX(0);
  background: #fff;
  box-shadow: 0 0.3rem 0.8rem rgba(0, 0, 0, 0.12);
  will-change: transform;
}

.bsa-sidebar .bsa-sidebar-header {
  width: 100%;
  height: 80px;
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 0 15px;
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: center;
  background-clip: padding-box;
  border-bottom: 1px solid rgba(127, 113, 113, 0.08);
}

.bsa-sidebar .bsa-sidebar-header .bsa-logo-icon {
  width: 36px;
  flex-shrink: 0;
  margin-right: 8px;
}

.bsa-sidebar .bsa-sidebar-header .bsa-logo-text {
  margin-left: 10px;
  margin-bottom: 0;
  letter-spacing: 1px;
  color: #4d5259;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}

.bsa-sidebar .bsa-sidebar-body {
  padding: 0;
  margin: 0;
  list-style: none;
  position: absolute;
  top: 80px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  overflow-x: hidden;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.bsa-sidebar .bsa-sidebar-body::-webkit-scrollbar {
  display: none;
}

.bsa-sidebar .bsa-sidebar-body ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

@media (max-width: 991.98px) {
  .bsa-sidebar {
    transform: translateX(-101%);
    box-shadow: none;
  }
}

.bsa-sidebar.open {
  transform: translateX(0);
}

.bsa-sidebar-body > ul li + li, .bsa-sidebar-body > ul li:first-child {
  padding-top: 5px;
}

.bsa-sidebar-body > ul li:last-child {
  padding-bottom: 5px;
}

.bsa-sidebar-body > ul a {
  position: relative;
  padding-left: 16px;
  padding-right: 24px;
  color: #5f5f5f;
  text-decoration: none;
  height: 40px;
  line-height: 40px;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  cursor: pointer;
}

.bsa-sidebar-body > ul a.active, .bsa-sidebar-body > ul a:active, .bsa-sidebar-body > ul a:focus, .bsa-sidebar-body > ul a:hover {
  color: #0d6efd;
  text-decoration: none;
  background: rgba(13, 110, 253, 0.12);
}

.bsa-sidebar-body > ul a.has-children:after {
  font-family: bootstrap-icons, serif;
  content: "\F284";
  transition: transform 0.3s linear;
  transform: scale(0.5) rotate(0deg);
  position: absolute;
  right: 24px;
}

.bsa-sidebar-body > ul a.has-children.open {
  color: #0d6efd;
  text-decoration: none;
  background: rgba(13, 110, 253, 0.12);
}

.bsa-sidebar-body > ul a.has-children.open:after {
  transform: scale(0.5) rotate(-90deg);
}

.bsa-sidebar-body > ul a.has-children.open + ul {
  display: block;
}

.bsa-sidebar-body > ul > li > a > i {
  margin-right: 10px;
}

.bsa-sidebar-body > ul ul {
  transition: height 150ms linear;
  display: none;
  border-bottom: 1px solid rgba(127, 113, 113, 0.08);
}

.bsa-sidebar-body > ul ul a {
  padding-left: calc(16px + 1rem + 10px);
}

.bsa-sidebar-body > ul ul ul a {
  padding-left: calc(16px + 2rem + 10px);
}

.bsa-sidebar-body > ul ul ul ul a {
  padding-left: calc(16px + 3rem + 10px);
}

.bsa-sidebar-body > ul ul ul ul ul a {
  padding-left: calc(16px + 4rem + 10px);
}

.bsa-sidebar-body > ul ul ul ul ul ul a {
  padding-left: calc(16px + 5rem + 10px);
}

@media (max-width: 991.98px) {
  .bsa-mask {
    z-index: 90;
    border-radius: inherit;
    position: fixed;
    left: 0;
    bottom: 0;
